.square {
 position: relative;
 overflow: hidden;
} 
.square:before {
 content: "";
 display: block;
 padding-top: 100%;
} 
.square__body {
 position: absolute;
 top: 0;
 right: 0;
 bottom: 0;
 left: 0;
} 
.circle {
 border-radius: 50%;
 overflow: hidden;
} 
form {
 margin: 0;
} 
.och-tracker input,
.och-tracker select,
.och-tracker textarea,
.input-like {
 font-size: 16px; 
 font-weight: 400;
 width: 100%;
 padding: 12px;
 display: block;
 border-radius: 0;
 -webkit-box-shadow: none;
         box-shadow: none;
 -webkit-appearance: none;
} 
.och-tracker input,
.och-tracker select,
.input-like {
 background-color: #fff;
 border: 1px solid #cccccc;
} 
.input-icon {
 position: relative;
} 
.input-icon > i {
 position: absolute;
 top: 14px;
 left: 14px;
 z-index: 1;
} 
.input-small {
 font-size: 12px;
 padding: 6px;
} 
.input-icon > i + * {
 padding-left: 38px;
} 
.och-tracker input[type="checkbox"] {
 width: auto;
} 
.och-tracker input::-webkit-input-placeholder {
 text-overflow: ellipsis;
} 
.och-tracker input:-ms-input-placeholder {
 text-overflow: ellipsis;
} 
.och-tracker input::placeholder {
 text-overflow: ellipsis;
} 
.och-tracker input.transparent {
 color: var(--textLighest);
 background: none;
 border: none;
 outline: none;
} 
form.submitted .input.invalid {
 border: 1px solid #ca0000;
} 
.radio {
 background: #fff;
 width: 24px;
 height: 24px;
 position: relative;
 margin-right: 8px;
} 
.radio,
.radio .bubble {
 border-radius: 50%;
} 
.radio .bubble {
 background: #0096FF;
 position: absolute;
 top: 4px;
 right: 4px;
 bottom: 4px;
 left: 4px;
 opacity: 0;
 -webkit-transition: opacity 0.225s ease-out;
 transition: opacity 0.225s ease-out;
} 
.radio input {
 position: absolute;
 opacity: 0;
} 
.radio input:checked + .bubble {
 opacity: 1;
} 
/* Button */ 
.blank {
 border: none;
 margin: 0;
 padding: 0;
 width: auto;
 overflow: visible;
 background: transparent;
 outline: none;

 /* inherit font & color from ancestor */
 color: inherit;
 font: inherit;

 /* Normalize `line-height`. Cannot be changed from `normal` in Firefox 4+. */
 line-height: normal;
 /* Corrects font smoothing for webkit */
 -webkit-font-smoothing: inherit;
 -moz-osx-font-smoothing: inherit;
 /* Corrects inability to style clickable `input` types in iOS */
 -webkit-appearance: none;
} 
/* Remove excess padding and border in Firefox 4+ */ 
&::-moz-focus-inner {
 border: 0;
 padding: 0;
} 
.button {
 font-size: 18px;
 font-weight: bold;
 padding: 12px 24px 11px;
 border: none;
 border-radius: 0;
 outline: none;
 display: block; 
 position: relative;
 -webkit-transition: background color 0.225s ease-out;
 transition: background color 0.225s ease-out;
} 
.button:after {
 content: "";
 position: absolute;
 top: 0;
 right: 0;
 bottom: 0;
 left: 0;
 -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, .14), 0 1px 5px 0 rgba(0, 0, 0, .12), 0 3px 1px -2px rgba(0, 0, 0, .2);
         box-shadow: 0 2px 2px 0 rgba(0, 0, 0, .14), 0 1px 5px 0 rgba(0, 0, 0, .12), 0 3px 1px -2px rgba(0, 0, 0, .2);
 opacity: 0;
} 
.button:hover:after {
 opacity: 1;
} 
.button.text {
 font-family: inherit;
 font-size: 14px;
 line-height: 12px;
 font-weight: normal;
 background: none;
 border: none;
 padding: 0;
 display: inline-block;
} 
.button.text:after {
 display: none !important;
} 
.button.rounded {
 line-height: 32px;
} 
.button.rounded,
.button.rounded:after {
 border-radius: 24px;
} 
.button.outline {
 background: none;
 border: 2px solid;
} 
.button.primary,
.button.lightest:hover,
.button.lightest:focus,
.button.lightest.active {
 color: #fff;
 background-color: #0096FF;
} 
.button.primary.outline {
 color: #0096FF;
 border-color: #0096FF;
} 
.button.quaternary {
 color: #414042;
 background-color: #fff;
 border: 1px solid #dddddd;
} 
.button.quaternary.active {
 color: #fff;
 background-color: #3d3d3d;
} 
.button.secondary {
 color: #fff;
 background-color: #01193C;
} 
.button.secondary:hover,
.button.secondary:focus,
.button.tertiary:hover,
.button.tertiary:focus,
.button.tertiary.active,
.button.secondary.active {
 color: #414042;
 background-color: #b7b7b7;
} 
.button.secondary.outline {
 color: #01193C;
 border-color: #01193C;
} 
.button.primary:hover,
.button.tertiary {
 color: #fff;
 background-color: #3d3d3d;
} 
/*
.button.tertiary:hover {
 color: var(--textDark);
 background-color: var(--secondaryColor);
}
*/ 
.button.tertiary.outline {
 color: #3d3d3d;
 border-color: #3d3d3d;
} 
.button.lightest.outline {
 color: #fff;
 background: transparent;
 border-color: #fff;
} 
.button.lightest {
 color: #414042;
 background-color: #fff;
} 
