.och-map-ripple {
  height: 20px;
  width: 20px;
  border-radius: 50%;
  position:relative;
  -webkit-transform:translate(-50%,-50%);
          transform:translate(-50%,-50%);
  cursor: pointer;
} 
.och-map-ripple:before,
.och-map-ripple:after {
  content:'';
  display:block;
  position:absolute;
  top:0; right:0; bottom:0; left:0;
  border-radius:50%;
  border:1px solid #0096FF;
} 
.och-map-ripple:before {
  -webkit-animation: ripple 2s linear infinite;
  animation: ripple 2s linear infinite;
} 
.och-map-ripple:after {
  -webkit-animation: ripple 2s linear 1s infinite;
  animation: ripple 2s linear 1s infinite;
} 
.och-expand-selector__full button.seal,
.och-map-ripple.seal:before,
.och-map-ripple.seal:after {
 border-color: #bd6228;
} 
.och-expand-selector__full button.turtle,
.och-map-ripple.turtle:before,
.och-map-ripple.turtle:after {
 border-color: #489e25;
} 
.och-expand-selector__full button.dolphin,
.och-map-ripple.dolphin:before,
.och-map-ripple.dolphin:after {
 border-color: #58aee3;
} 
.och-expand-selector__full button.whale,
.och-map-ripple.whale:before,
.och-map-ripple.whale:after {
 border-color: #EBA900;
} 
@-webkit-keyframes ripple{
  0% {-webkit-transform:scale(1);transform:scale(1); }
  75% {-webkit-transform:scale(1.75);transform:scale(1.75); opacity:1;}
  100% {-webkit-transform:scale(2);transform:scale(2); opacity:0;}
} 
@keyframes ripple{
  0% {-webkit-transform:scale(1);transform:scale(1); }
  75% {-webkit-transform:scale(1.75);transform:scale(1.75); opacity:1;}
  100% {-webkit-transform:scale(2);transform:scale(2); opacity:0;}
} 
