/* Waves */ 
.waves > div {
 content: "";
 height: 11px;
 position: absolute;
 top: -11px;
 right: -20%;
 left: -20%;
 -webkit-animation: moveWavesLeft 1s linear infinite;
         animation: moveWavesLeft 1s linear infinite;
 -webkit-animation-fill-mode: forwards;
         animation-fill-mode: forwards;
} 
.tilt--left .waves:before {
 -webkit-animation-direction: reverse;
         animation-direction: reverse;
} 
@-webkit-keyframes moveWavesLeft {
 0% {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
 }

 100% {
  -webkit-transform: translate3d(46px, 0, 0);
          transform: translate3d(46px, 0, 0);
 }
} 
@keyframes moveWavesLeft {
 0% {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
 }

 100% {
  -webkit-transform: translate3d(46px, 0, 0);
          transform: translate3d(46px, 0, 0);
 }
} 
